/* リキッドレイアウト対応 */
@use "global" as *

html
    position: relative
    overflow-x: hidden
    //~375px
    @media (max-width: 375px)
        font-size: vw(375, 16)

    //375px~787px
    font-size: 16px

    //inner~max-screen
    @media (min-width: $inner)
        font-size: 16px

/* pcの電話番号発信対応 */
a[href^="tel:"]
    +md
        pointer-events: none

/* ホバー */
a
    text-decoration: none
    color: inherit
    transition: opacity 0.3s
    +md
        &:hover
            opacity: 0.8

body
    display: none
    position: relative
    overflow-x: hidden

/* セクション */
.section
    +md
        padding: 0 rem(67)

.section-title
    font-weight: bold
    font-size: rem(36)
    text-align: center
    +md
        font-size: rem(24)
   