@use '../global' as *

/* SNS */
.sns
    display: flex
    align-items: flex-start
    +md
        flex-direction: row
        justify-content: center
        // margin-left: rem(50)
.sns-item
    padding: .5em 1em
    transition: .5s
    &:hover
        transform: translateY(-10px)
        transition: .5s
    &.twitter
        padding: .8em 1em
