@use '../global' as c

/* スペーシングクラス */

@for $i from 1 through 5
    .mt-#{$i}
        margin-top: $i*0.5rem !important
    .mb-#{$i}
        margin-bottom: $i*0.5rem !important
    .ml-#{$i}
        margin-left: $i*0.5rem !important
    .mr-#{$i}
        margin-right: $i*0.5rem !important
    .pt-#{$i}
        margin-top: $i*0.5rem !important
    .pb-#{$i}
        margin-bottom: $i*0.5rem !important
    .pl-#{$i}
        margin-left: $i*0.5rem !important
    .pr-#{$i}
        margin-right: $i*0.5rem !important
    
.mx-auto
    margin-left: auto !important
    margin-right: auto !important

.img-fluid
    width: 100%