@use '../global' as *

$imgPath: '../img/'
    

/* メインビジュアル */
.hero
    height: 600px
    margin: 0 auto
    background-image: url('#{$imgPath}hero.jpeg')
    background-size: cover
    background-position: center
    background-repeat: no-repeat
    position: relative
    .catch
        background-color: rgba(255, 255, 255, 0.75)
        position: absolute
        bottom: 0
        width: 100%
        padding: rem(40)
        color: $accent
        text-align: center
        .sub-text
            font-size: rem(18)
            margin-bottom: rem(16)
        .main-text
            font-size: rem(44)
            font-weight: bold
            +md
                font-size: rem(35)

/* リード */
.lead
    padding: rem(80) 0
    background-color: $accent
    color: $white
    .section-title
        margin-bottom: rem(38)
    .container
        max-width: 640px


/* サービス */
.service
    padding: rem(95) rem(3)
    text-align: center
    .container
        max-width: 1160px
    .section-title
        color: $accent
        display: inline-block
        margin: 0 auto
        border-bottom: 1px dotted $accent
    .section-subTitle
        color: $accent
        margin-bottom: rem(75)
        +md
            margin-bottom: rem(53)

    .card
        width: calc(100% / 3 - 40px * 2 / 3) //横に加えたい余白 * (全体の数-1) / 全体の数
        display: flex
        flex-direction: column
        margin-top: 16px
        margin-right: 40px
        border: none
        &:last-of-type
            margin-right: 0
        +md
            width: 100%
            margin-left: auto
            margin-right: auto !important

    .card__img
        height: rem(300)
        background-size: cover
        background-position: center
        &.office
            background-image: url('#{$imgPath}office.jpg')
        &.management
            background-image: url('#{$imgPath}manage.jpg')
        &.research
            background-image: url('#{$imgPath}research.jpg')
        +lg
            height: vw(1200,300)
        +md
            height: rem(246)

    .card__body
        display: flex
        flex-direction: column
        height: auto
        flex: 1
        .card-title
            font-size: rem(30)
            font-weight: bold
            margin: rem(26) 0
            +md
                font-size: rem(20)
        .card-text
            margin-bottom: rem(25)

    .card__btn
        text-transform: uppercase
        display: block
        margin-top: auto


/* お問い合わせフォーム */
.contact
    position: relative
    z-index: 2
    .container
        max-width: 1240px
        +md
            padding-left: 0     
            padding-right: 0     

.contact-form
    display: flex
    flex-direction: row
    background-color: #f8f2ee
    margin: 0 auto
    padding: rem(90)
    max-width: 1240px
    +lg
        padding: rem(90) rem(45)
    +md
        padding: rem(90) rem(30)
        flex-direction: column
    div
        width: 50%
        margin-bottom: rem(20)
        +md
            width: 100%
    .title
        font-size: rem(36)
        font-weight: bold
        margin-bottom: rem(30)
    .form-desc
        margin-right: rem(30)
    .form-input
        display: flex
        flex-direction: column
    .roundedBtn
        max-width: 272px
        color: $white
        background-color: $accent
        &:hover
            border-color: $accent
            background-color: $white
            color: $accent
        +md
            max-width: 100%

.form-group
    input,textarea
        width: 100%
        padding: 10px
        background-color: #fff
        border: 1px solid #CDD6DD
        margin-bottom: rem(17)