@use '../global' as *

////////////////////
// 非表示設定
////////////////////

.pc-only
    display: block
.sp-only    
    display: none

+md
    .pc-only
        display: none
    .sp-only    
        display: block

.hidden-pc
    +pc-only
        display: none
.hidden-tab
    +tab-only
        display: none

.hidden-sp
    +sp-only
        display: none