@use '../global' as *


/* サイトタイトル */
.site
    padding-left: 0px
    color: #000
    width: 450px
    font-size: rem(22)
    text-align: center
    // padding: rem(30)
    // width: vw(100,25)
    display: flex
    flex-direction: row
    align-items: center
    +sm
        width: 100%
        padding-left: 0

.site div
    text-align: left

.site__logo
    width: 55px
    height: 55px
    margin-right: 15px
    img
        width: 100%

.site__title
    font-weight: bold
    font-size: 20px

.site__desc
    font-size: 12px
    color: #707F89