@use '../global' as *

// ヘッダー
.header
    padding: 10px 0
    +md
        .site
            .site__logo
                width: 60px
                height: 60px
                margin-right: 5px
            .site__title
                font-weight: bold
                font-size: 20px
            .site__desc
                font-size: 14px
                color: #707F89
    .container
        width: 100%
        max-width: 1240px
        margin-left: auto
        margin-right: auto
        display: flex
        flex-direction: row
        justify-content: space-between
        align-items: center
        +md
            width: 100%
        
    .gnavi
        font-weight: bold
        .roundedBtn
            min-width: 148px
            padding: rem(6) rem(15)
            border: 1px solid $main
            +md
                background-color: transparent
                min-width: 208px
                color: $white
                border: 1px solid $white
    

    .menu-item:not(:last-child)
        &:after
            content: ''
            display: block
            width: 0%
            height: 2px
            border-radius: 10px
            background-color: $accent
            transition: .5s
        &:hover
            &:after
                width: 100%
                transition: .5s

.is-open
    .gnavi
        // transform: translateX(0)
        left: 0
        transition: .5s
    .nav-button
        color: #ffffff
        &::before
            box-shadow: none
            transform: translateY(2px) rotateZ(45deg)
            transition: .5s
        &::after
            transform: rotate(-45deg)
            transition: .5s