@use '../global' as *
    
/* ハンバーガーメニュー */
.nav-button
    display: none
    +md
        display: block
        position: relative
        z-index: 100
        box-sizing: content-box
        // padding: rem(30)
        outline: none
        border: none
        background: none
        width: 25px
        height: 14px
        cursor: pointer
        color: $accent 
        &::before,
        &::after
            content: ''
            display: block
            height: 2px
            background-color: currentColor
            transform: translateY(6px)
            transition: .5s
        &::before
            transform: translateY(-6px)
            box-shadow: 0 7px currentColor
    