@use '../global' as *


/* フッター */
.footer
    height: 340px
    margin-top: -50px
    padding-top: rem(100)
    background-color: $accent
    color: #ffffff
    .site
        align-items: flex-start
    .site__logo
        width: 60px
        height: 60px
        margin-right: 15px
        // +md
        //     width: 40px
        //     height: 40px
    .site__title
        font-weight: bold
        font-size: 20px
        margin: rem(20) 0
        color: #ffffff
        +md
            font-size: 20px
            margin: rem(10) 0
    .site__desc
        font-size: 12px
        color: #ffffff
        line-height: 2

    .container
        max-width: 1240px
        margin: 0 auto
        display: flex
        flex-direction: row
        justify-content: space-between
        +md
            max-width: 95%
            padding: 0 rem(30)
            flex-direction: column

.footer-logo
    color: black