@use '../global' as *


/* ヘッダーナビ */
.gnavi
    width: 100%
    flex-grow: 1
    display: flex
    align-items: center
    +md
        position: fixed
        top: 0
        left: 100%
        // transform: translateX(100vw)
        background-color: rgba(0, 0, 0,0.8)
        height: 100vh
        width: 100%
        z-index: 2
        padding: 0
        transition: .5s
.menu-items
    width: 100%
    display: flex
    flex-direction: row
    justify-content: flex-end
    align-items: center
    +md
        color: $white
        align-items: center
        flex-direction: column
    
.menu-item
    padding: .5em 1em
    +md
        .menu-item
            font-size: 16px
            font-weight: bold
            margin-bottom: rem(30) 