////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）

// サイトカラー
$base: #eef4f8 
$main: #cdd6dd
$accent: #eaa00a
$black: #333333
$white: #ffffff

// リンクカラー
$link-color: #eaa00a
$hover-color: #eaa00a



// 文字リンクカラー
=link-color($normal,$hover)
    color: $normal
    &:hover
        color: $hover
        text-decoration: none